<template>
  <el-row
    class="main-header"
    :gutter="10"
    :style="{ 'height': height }">
    <Join ref="modal"/>
    <Login ref="modal1" :content="modalContent" />
    <div class="hide-on-small">
      <div class="logo-wrapper" @click="clickLogo"><div class="ic ic-logo"/></div>
      <div class="tool-wrapper">
        <div class="search-field">
          <el-input
            placeholder="검색"
            prefix-icon="el-icon-search"
            v-model="state.searchValue">
          </el-input>
        </div>
        <div class="button-wrapper" v-if="!token">
          <el-button type="primary" el-icon class="el-icon-ci" @click="handleClick">회원가입</el-button>
          <el-button type="primary" @click="clickLogin">로그인</el-button>
        </div>
      </div>
    </div>
    <div class="hide-on-big">
      <div class="menu-icon-wrapper" @click="changeCollapse"><i class="el-icon-menu"></i></div>
      <div class="logo-wrapper" @click="clickLogo"><div class="ic ic-logo"/></div>
      <div class="menu-icon-wrapper"><i class="el-icon-search"></i></div>
      <div class="mobile-sidebar-wrapper" v-if="!state.isCollapse">
        <div class="mobile-sidebar">
          <div class="mobile-sidebar-tool-wrapper">
            <div class="logo-wrapper"><div class="ic ic-logo"/></div>
            <br>
          </div>
          <el-menu
            :default-active="String(state.activeIndex)"
            active-text-color="#ffd04b"
            class="el-menu-vertical-demo"
            @select="menuSelect">
            <el-menu-item type="primary" @click="handleClick" v-show="!token">회원가입</el-menu-item>
            <el-menu-item type="primary" @click="clickLogin" v-show="!token">로그인</el-menu-item>
            <el-menu-item v-for="(item, index) in state.menuItems" :key="index" :index="index.toString()">
              <i v-if="item.icon" :class="['ic', item.icon]"/>
              <span>{{ item.title }}</span>
            </el-menu-item>

          </el-menu>
        </div>
        <div class="mobile-sidebar-backdrop" @click="changeCollapse"></div>
      </div>
    </div>
  </el-row>
</template>
<script>
import { reactive, computed } from 'vue'
import { useStore } from 'vuex'
import { useRouter } from 'vue-router'
import { ref } from "vue";
import Login from "@/views/main/components/login-dialog.vue";
import Join from "@/views/main/components/Join-Member.vue";
export default {
  name: 'main-header',
  components:{
    Login,
    Join
  },
  props: {
    height: {
      type: String,
      default: '70px'
    }
  },

  setup(props, { emit }) {
    const modal = ref(null);
    const modal1 = ref(null);
    const result = ref("");
    const store = useStore()
    const token=localStorage.getItem('jwt')
    const router = useRouter()
    const state = reactive({
      searchValue: null,
      menuItems: computed(() => {
        const MenuItems = store.getters['menuStore/getMenus']
        // const loadingInstance1 = ElLoading.service({ fullscreen: true })
        let keys = Object.keys(MenuItems)
        let menuArray = []
        for (let i = 0; i < keys.length; ++i) {
          let menuObject = {}
          menuObject.icon = MenuItems[keys[i]].icon
          menuObject.title = MenuItems[keys[i]].name
          if(token === null && menuObject.title === "지난 회의 이력"){
            continue;
          }else{
            menuArray.push(menuObject)
          }

      }
      console.log(menuArray)
      // loadingInstance1.close()
      return menuArray
        }),
      activeIndex: computed(() => store.getters['menuStore/getActiveMenuIndex'])
      })

    if (state.activeIndex === -1) {
      state.activeIndex = 0
      store.commit('menuStore/setMenuActive', 0)
    }

    // async-await을 사용하여, Modal로부터 응답을 기다리게 된다.
    const handleClick = async () => {
      const ok = await modal.value.show();
      if (ok) {
        result.value = "확인을 눌렀군요!";
      } else {
        result.value = "취소를 눌렀네요?";
      }
    };
    const clickLogin = async () => {
      const ok = await modal1.value.show();
      if (ok) {
        result.value = "확인을 눌렀군요!";
      } else {
        result.value = "취소를 눌렀네요?";
      }
    };
    const menuSelect = function (param) {
      store.commit('menuStore/setMenuActive', param)
      const MenuItems = store.getters['menuStore/getMenus']
      let keys = Object.keys(MenuItems)
      router.push({
        name: keys[param]
      })
    }

    const clickLogo = () => {
      store.commit('menuStore/setMenuActive', 0)
      const MenuItems = store.getters['menuStore/getMenus']
      let keys = Object.keys(MenuItems)
      router.push({
        name: keys[0]
      })
    }

    // const clickLogin = () => {
    //   emit('openLoginDialog')
    // }

    // const clickJoin = () => {
    //   emit('openJoinMember')
    // }

    const changeCollapse = () => {
      state.isCollapse = !state.isCollapse
    }
    const clickLogout=()=>{
      localStorage.removeItem('token')
      const token =false
    }
    return {
      state,
      menuSelect,
      clickLogo,
      // clickLogin,
      // clickJoin,
      changeCollapse,
      modal,
      modal1,
      result,
      handleClick,
      clickLogin,
      clickLogout,
      token,
    }
  }
}
</script>
