<template>
  <Modal ref="baseModal" >
      <h1 style="text-align:center;">회원가입</h1>
      <el-form :model="state.form" :rules="state.rules" ref="loginForm" :label-position="state.form.align">
      <el-form-item prop="department" label="소속" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.department" autocomplete="off" placeholder="30자까지 입력가능합니다"></el-input>
      </el-form-item>
      <el-form-item prop="position" label="직책" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.position" autocomplete="off" placeholder="30자까지 입력가능합니다"></el-input>
      </el-form-item>
      <el-form-item prop="name" label="이름" :label-width="state.formLabelWidth" >
        <el-input v-model="state.form.name" autocomplete="off" placeholder="30자까지 입력가능합니다"></el-input>
      </el-form-item>
      <el-form-item prop="id" label="아이디" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.id" autocomplete="off" placeholder="16자까지 입력가능합니다"></el-input>
        <el-button @click="duplicate(state.form.id)" id="duplicate_check">중복체크</el-button>
      </el-form-item>
      <el-form-item prop="password" label="비밀번호" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.password" autocomplete="off"  show-password placeholder="9~13글자, 영문+숫자+특수문자"></el-input>
      </el-form-item>
      <el-form-item prop="confirm" label="비밀번호 확인" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.confirm" autocomplete="off"  show-password placeholder="비밀번호를 다시 입력해주세요"></el-input>
      </el-form-item>
    </el-form>
    <div class="buttons-container">
      <el-button class="btn confirm" type="primary" @click="clickJoin" style="width:70%;">확인</el-button>
      <el-button class="btn confirm" type="danger" @click="cancel" style="width:20%;">취소</el-button>
    </div>
  </Modal>
</template>
<style>
.login-dialog {
  width: 400px !important;
  margin:auto;
  background-color:white;
  padding:10px;
  border-radius: 10px;
}
.login-dialog .el-dialog__headerbtn {
  float: right;
  background-color:red;
}
.login-dialog .el-form-item__content {
  margin-left: 0 !important;
  float: right;
  width: 200px;
  display: inline-block;
}
.login-dialog .el-form-item {
  margin-bottom: 20px;
}
.login-dialog .el-form-item__error {
  font-size: 12px;
  color: red;
}
.login-dialog .el-input__suffix {
  display: none;
}
.login-dialog .el-dialog__footer {
  margin: 0 calc(50% - 80px);
  padding-top: 0;
  display: inline-block;
}
.login-dialog .dialog-footer .el-button {
  width: 120px;
}
</style>
<script>
import Modal from "@/views/main/components/Modal.vue";
import { assertExpressionStatement } from "@babel/types";
import { reactive, computed, ref, onMounted } from 'vue'
import { useStore } from 'vuex'
// import VueAxios from 'vue-axios'
import axios from 'axios'
import { ElLoading } from 'element-plus'
export default {
  name: "Join-Member",
  components: {
    Modal,
  },
  // 렌더링할 텍스트를 가져옵니다.
  props: {
    content: Array,
    open: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    // 자식 컴포넌트를 핸들링하기 위한 ref
    const baseModal = ref(null);
    // Promise 객체를 핸들링하기 위한 ref
    const resolvePromise = ref(null);
    const store = useStore()
    // 마운드 이후 바인딩 될 예정 - 컨텍스트에 노출시켜야함. <return>
    const loginForm = ref(null)
    const checkbtn=false
    const idcheck=true
    /*
      // Element UI Validator
      // rules의 객체 키 값과 form의 객체 키 값이 같아야 매칭되어 적용됨
      //
    */
    const show = () => {
      // baseModal을 직접 컨트롤합니다.
      baseModal.value.open();
      // Promise 객체를 사용하여, 현재 모달에서 확인 / 취소의
      // 응답이 돌아가기 전까지 작업을 기다리게 할 수 있습니다.
      return new Promise((resolve, _) => {
        // resolve 함수를 담아 외부에서 사용합니다.
        resolvePromise.value = resolve;
      });
    };
    const state = reactive({
      form: {
        id: '',
        password: '',
        confirm: '',
        department:'',
        position:'',
        name:'',
        align: 'left',
      },
      rules: {
        id: [
          // { required: true, message: 'Please input ID', trigger: 'blur' },
          // { max:16,message:'최대 16자까지 입력 가능합니다.',trigger:'blur'}
          {validator (rule, value, callback) {
            if (value) {
              // form.check_id=true
              console.log(form.check_id)
              callback();
            } else {
              // form.check_id=false
              callback(new Error('아이디를 입력해주세요'));
            }
          }, trigger:'blur'
          },
          {validator (rule, value, callback) {
            if (value.length<=16) {
              // form.check_id=true
              callback();
            } else {
              // form.check_id=false
              callback(new Error('최대 16자까지 입력 가능합니다.'));
            }
          }, trigger:'blur'
          },
        ],
        password: [
          // { required: true, message: 'Please input password', trigger: 'blur' },
          // { min:9, message:'최소 9글자 이상 입력해야 합니다.',trigger:'blur'},
          // { max:13, message:'최대 13글자까지 입력 가능합니다.',trigger:'blur'},
          {validator (rule, value, callback) {
            if (value) {
              // form.check_password=true
              callback();
            } else {
              // form.check_password=false
              callback(new Error('비밀번호를 입력해주세요'));
            }
          }, trigger:'blur'
          },
          {validator (rule, value, callback) {
            if (value.length<=13 && value.length>=9) {
              // form.check_password=true
              callback();
            } else {
              // form.check_password=false
              callback(new Error('9~13자리로 입력해주세요'));
            }
          }, trigger:'blur'
          },
          {validator (rule, value, callback) {
            if (/^.*(?=^.{8,15}$)(?=.*\d)(?=.*[a-zA-Z])(?=.*[!@#$%^&+=]).*$/.test(value)) {
              // form.check_password=true
              callback();
            } else {
              // form.check_password=false
              callback(new Error('영문, 숫자, 특수문자를 포함하여 작성해 주세요.'));
            }
          }, trigger:'blur'
          }
        ],
        confirm: [
          // { required: true, message: 'Please input password again', trigger: 'blur' },
          // { min:9, message:'최소 9글자 이상 입력해야 합니다.',trigger:'blur'},
          // { max:13, message:'최대 13글자까지 입력 가능합니다.',trigger:'blur'},
          {validator (rule, value, callback) {
            if (value) {
              // form.check_confirm=true
              callback();
            } else {
              // form.check_confirm=false
              callback(new Error('비밀번호를 다시 입력해주세요'));
            }
          }, trigger:'blur'
          },
          {validator (rule, value, callback) {
            if (state.form.password==value) {
              // form.check_confirm=true
              callback(new Error('비밀번호가 일치합니다.'));
            } else {
              // form.check_confirm=false
              callback(new Error('비밀번호가 일치하지 않습니다.'));
            }
          }, trigger:'blur'
          },
        ],
        name: [
          // { required: true, message: 'Please input name', trigger: 'blur' },
          // { max:30,message:'최대 30자까지 입력 가능합니다.',trigger:'blur'}
          {validator (rule, value, callback) {
            if (value) {
              // form.check_name=true
              callback();
            } else {
              // form.check_name=false
              callback(new Error('이름을 입력해주세요'));
            }
          }, trigger:'blur'
          },
          {validator (rule, value, callback) {
            if (value.length<=30) {
              // form.check_name=true
              callback();
            } else {
              // form.check_name=false
              callback(new Error('최대 30자까지 입력 가능합니다.'));
            }
          }, trigger:'blur'
          },
        ],
        position: [
          // { max:30,message:'최대 30자까지 입력 가능합니다.',trigger:'blur'}
          {validator (rule, value, callback) {
            if (value.length<=30) {
              // form.check_position=true
              callback();
            } else {
              // form.check_position=false
              callback(new Error('최대 30자까지 입력 가능합니다.'));
            }
          }, trigger:'blur'
          },
        ],
        department: [
          // { max:30,message:'최대 30자까지 입력 가능합니다.',trigger:'blur'}
          {validator (rule, value, callback) {
            if (value.length<=30) {
              // form.check_department=true
              callback();
            } else {
              //  form.check_department=false
              callback(new Error('최대 30자까지 입력 가능합니다.'));
            }
          }, trigger:'blur'
          },
        ]
      },
      dialogVisible: computed(() => props.open),
      formLabelWidth: '120px'
    })

    onMounted(() => {
      // console.log(loginForm.value)
    })
    const duplicate=function(value){
      axios({
        method:'get',
        url:`http://localhost:8080/api/v1/users/${value}`,
      })
      .then((res)=>{
        console.log(res.data.message)
        alert(res.data.message)
      })
      .catch((res)=>{
        console.log(res.response)
        alert(res.response.data.message)
      })
    }
    const clickJoin = function () {
      console.log(
        state.form.id,
        state.form.password,
        state.form.name,
        state.form.position,
        state.form.department,
        )
      const user={
        'id':state.form.id,
        'password':state.form.password,
        'name':state.form.name,
        'position':state.form.position,
        'department':state.form.department
      }
      const loadingInstance1 = ElLoading.service({ fullscreen: true })
      axios({
        method:'post',
        url:'http://localhost:8080/api/v1/users/',
        data:user
      })
      .then((res)=>{
        loadingInstance1.close()
        state.form.id = ''
        state.form.password = ''
        state.form.department = ''
        state.form.position = ''
        state.form.name = ''
        state.form.confirm= ''
        alert("회원가입 성공!")
        console.log(res)
        baseModal.value.close();
        resolvePromise.value(false);
      })
    }

    const handleClose = function () {
      state.form.id = ''
      state.form.password = ''
      state.form.department = ''
      state.form.position = ''
      state.form.name = ''
      state.form.confirm= ''
      emit('closeLoginDialog')
    }

    const confirm = () => {
      baseModal.value.close();
      resolvePromise.value(true);
    };

    const cancel = () => {
      baseModal.value.close();
      resolvePromise.value(false);
    };
    return {loginForm, state, clickJoin, handleClose, baseModal, show, confirm, cancel,duplicate,idcheck };
  },
};
</script>
