<template>
  <Modal ref="baseModal" >
      <h1 style="text-align:center;">로그인</h1>
      <el-form :model="state.form" :rules="state.rules" ref="loginForm" :label-position="state.form.align">
      <el-form-item prop="id" label="아이디" :label-width="state.formLabelWidth" >
        <el-input style="width:200%" v-model="state.form.id" autocomplete="off" placeholder="16자까지 입력가능합니다"></el-input>
      </el-form-item>
      <el-form-item prop="password" label="비밀번호" :label-width="state.formLabelWidth">
        <el-input v-model="state.form.password" autocomplete="off" show-password placeholder="9~13글자, 영문+숫자+특수문자"></el-input>
      </el-form-item>
    </el-form><br>
    <div class="buttons-container">
      <el-button class="btn confirm" type="primary" @click="clickLogin" style="width:70%;">확인</el-button>
      <el-button class="btn confirm" type="danger" @click="cancel" style="width:20%;">취소</el-button>
    </div>
  </Modal>
</template>
<style>
.login-dialog {
  width: 400px !important;
  margin:auto;
  background-color:white;
  padding:10px;
  border-radius: 10px;
}
.login-dialog .el-dialog__headerbtn {
  float: right;
  background-color:red;
}
.login-dialog .el-form-item__content {
  margin-left: 0 !important;
  float: right;
  width: 200px;
  display: inline-block;
}
.login-dialog .el-form-item {
  margin-bottom: 20px;
}
.login-dialog .el-form-item__error {
  font-size: 12px;
  color: red;
}
.login-dialog .el-input__suffix {
  display: none;
}
.login-dialog .el-dialog__footer {
  margin: 0 calc(50% - 80px);
  padding-top: 0;
  display: inline-block;
}
.login-dialog .dialog-footer .el-button {
  width: 120px;
}
</style>
<script>
// import VueAxios from 'vue-axios'
import axios from 'axios'
import Modal from "@/views/main/components/Modal.vue";
import { reactive, computed, ref, onMounted } from 'vue'
import { useStore } from 'vuex'

export default {
  name: "login-dialog",
  components: {
    Modal,
  },
  // 렌더링할 텍스트를 가져옵니다.
  props: {
    content: Array,
    open: {
      type: Boolean,
      default: false
    }
  },
  setup(props, { emit }) {
    // 자식 컴포넌트를 핸들링하기 위한 ref
    const baseModal = ref(null);
    // Promise 객체를 핸들링하기 위한 ref
    const resolvePromise = ref(null);
    const store = useStore()
    // 마운드 이후 바인딩 될 예정 - 컨텍스트에 노출시켜야함. <return>
    const loginForm = ref(null)

    /*
      // Element UI Validator
      // rules의 객체 키 값과 form의 객체 키 값이 같아야 매칭되어 적용됨
      //
    */
    const show = () => {
      // baseModal을 직접 컨트롤합니다.
      baseModal.value.open();
      // Promise 객체를 사용하여, 현재 모달에서 확인 / 취소의
      // 응답이 돌아가기 전까지 작업을 기다리게 할 수 있습니다.
      return new Promise((resolve, _) => {
        // resolve 함수를 담아 외부에서 사용합니다.
        resolvePromise.value = resolve;
      });
    };
    const state = reactive({
      form: {
        id: '',
        password: '',
        align: 'left'
      },
      rules: {
        id: [
          { required: true, message: 'Please input ID', trigger: 'blur' },
          { max : 16, message : '최대 16자까지 입력 가능합니다.', trigger: 'blur'}
        ],
        password: [
          { required: true, message: 'Please input password', trigger: 'blur' },
          { min:9, message:'최소 9글자 이상 입력해야 합니다.',trigger:'blur'},
          { max:13, message:'최대 13글자까지 입력 가능합니다.',trigger:'blur'},
          {validator (rule, value, callback) {
            if (/^.*(?=^.{8,15}$)(?=.*\d)(?=.*[a-zA-Z])(?=.*[!@#$%^&+=]).*$/.test(value)) {
              callback();
            } else {
              callback(new Error('영문, 숫자, 특수문자를 포함하세요.'));
            }
          }, trigger:'blur'
          }
        ]
      },
      dialogVisible: computed(() => props.open),
      formLabelWidth: '120px'
    })

    onMounted(() => {
      // console.log(loginForm.value)
    })

    const clickLogin = function () {
      console.log(
        state.form.id,
        state.form.password,
      )

      const user={
        'id':state.form.id,
        'password':state.form.password,
      }
      axios({
        method:'post',
        url:'http://localhost:8080/api/v1/auth/login',
        data:user
      })
      .then((res)=>{
        alert("Login!")
        loginForm.value.validate(async (valid) => {
        if (valid) {
          console.log('submit')
          await store.dispatch('accountStore/loginAction', { id: state.form.id, password: state.form.password })
          console.log('accessToken ' + store.getters['accountStore/getToken'])
        }})
        localStorage.setItem("jwt", res.data.accessToken)
        baseModal.value.close();
        resolvePromise.value(false);
        window.location.reload(true);
      })
      .catch((res)=>{
        alert(res.response.data.message);
      })
      // 로그인 클릭 시 validate 체크 후 그 결과 값에 따라, 로그인 API 호출 또는 경고창 표시
    }

    const handleClose = function () {
      state.form.id = ''
      state.form.password = ''
      emit('closeLoginDialog')
    }

    const confirm = () => {
      baseModal.value.close();
      resolvePromise.value(true);
    };

    const cancel = () => {
      baseModal.value.close();
      resolvePromise.value(false);
    };
    return {loginForm, state, clickLogin, handleClose, baseModal, show, confirm, cancel };
  },
};
</script>
