<template>
  <el-container class="main-wrapper">
    <main-header
      :height="`70px`"
      @openLoginDialog="onOpenLoginDialog"
      @openJoinMember="onOpenJoinMember"
      />
    <el-container class="main-container">
      <el-aside class="hide-on-small" width="240px">
        <main-sidebar
          :width="`240px`"/>
      </el-aside>
      <el-main :class="{bgclick:loginDialogOpen, bgclick1:JoinMemberOpen}">
        <router-view :class="{click:loginDialogOpen, click1:JoinMemberOpen}" ></router-view>
        <login-dialog
          :open="loginDialogOpen"
          @closeLoginDialog="onCloseLoginDialog"/>
        <Join-Member
          :open="JoinMemberOpen"
          @closeJoinMember="onCloseJoinMember"/>
      </el-main>
    </el-container>
    <main-footer :height="`110px`"/>
  </el-container>
</template>
<style>
  @import "https://unpkg.com/element-plus/lib/theme-chalk/index.css";
  @import './main.css';
  @import '../../common/css/common.css';
  @import '../../common/css/element-plus.css';
 .click{
  display: none;
 }
 .click1{
  display: none;
 }
 .bgclick{
  background-color:lightgrey;
  opacity: 0.5;
 }
 .bgclick1{
  background-color:lightgrey;
  opacity: 0.5;
 }
</style>
<script>
import JoinMember from './components/Join-Member'
import LoginDialog from './components/login-dialog'
import MainHeader from './components/main-header'
import MainSidebar from './components/main-sidebar'
import MainFooter from './components/main-footer'

export default {
  name: 'Main',
  components: {
    MainHeader,
    MainSidebar,
    MainFooter,
    LoginDialog,
    JoinMember,
  },
  data () {
    return {
      loginDialogOpen: false,
      JoinMemberOpen:false,
    }
  },
  methods: {
    onOpenLoginDialog () {
      this.loginDialogOpen = true
      this.JoinMemberOpen = false
    },
    onCloseLoginDialog () {
      this.loginDialogOpen = false
      this.JoinMemberOpen = false
    },
    onOpenJoinMember () {
      this.JoinMemberOpen = true
      this.loginDialogOpen = false
    },
    onCloseJoinMember () {
      this.JoinMemberOpen = false
      this.loginDialogOpen = false
    }
  }
}
</script>
